
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { OfficialsealList, startApplication } from '@/api/'
import { mapGetters } from 'vuex'
export default {
name: 'abnormal',
components: {
PopupPicker, Group, XInput, PopupRadio, Datetime, XTextarea
},
data () {
return {
Officialseal: [],
endTime: '',
startTime: '',
OfficialType: [{ key: 1, value: '正本原件' }, {key: 2, value: '副本原件'}],
form: {
type: 22,
companyId: '',
loanItemId: '',
loanItemType: '',
reason: '',
loanTime: '',
returnTime: ''
}
}
},
methods: {
changeStartTime (val) {
this.form.loanTime = val + ':00'
},
changeEndTime (val) {
this.form.returnTime = val + ':00'
},
showseal () {
this.Officialseal = []
this.getOfficialsealList()
},
getOfficialsealList () {
var allsealList = []
const data = {
companyId: this.form.companyId,
type: 2
}
OfficialsealList(data).then(res => {
allsealList = res.result
allsealList.forEach(e => {
this.Officialseal.push({key: e.id, value: e.name})
})
})
},
startLeave () {
startApplication(this.form)
.then(res => {
this.$vux.toast.show({
text: res.message,
type: 'success'
})
this.$router.go(-1)
})
}
},
computed: {
...mapGetters(['applyLeaveList'])
}
}



